@import url(https://fonts.googleapis.com/css2?family=Libre+Caslon+Text:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Scope+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Asul&family=Scope+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{background-color:#363636;padding:30px}header h1{margin:0px;text-align:center;font-size:50px;color:#fdf5ee;font-family:'Libre Caslon Text', serif}

main{font-family:'Scope One', serif}main .url{text-align:left;margin:25px 0px}main .url input{width:350px;height:25px;margin:10px 0px}main .url .go{font-size:18px;font-family:'Scope One', serif}main .methodDiv{text-align:center;font-size:18px}main .methodDiv .method{font-size:15px;margin:20px 5px;padding:5px 25px;border:none;font-family:'Scope One', serif;background-color:#bdbdbd}main .methodDiv .method:hover{background-color:#ddd;background-color:#FFFFFF}main .output{padding:25px 50px;height:300px;width:90%;background-color:#EFEFEF;margin:10px auto;border-style:solid;border-color:#a8a6a6}

p{font-family:'Asul', sans-serif;background-color:#767676;padding:25px;margin-top:35px;margin-bottom:0px}

